.login{
  background: #ffffff;
  min-height: 100vh;
  padding-top: 1rem;
  .logo{
    width: 100px;
    display: -webkit-box;
    margin: 0 auto;
  }
  .disc{
    color: #999999;
    text-align: center;
    margin-top: .2rem;
    font-size: 14px;
  }
  .login-form{
    padding: .5rem .2rem;
    input{
      font-size: 14px !important;
    }
    .am-input-label{
      font-size: 14px;
    }
    .start-btn{
      width: 7rem;
      margin: 0 auto;
      display: block;
      font-size: 14px;
      margin-top: .6rem;
      background: #eb6101;
      &:hover{
        background: #eb6101;
      }
    }
  }
}
