/* Tab */
.change-detail .info-list {
  background: #ffffff;
}
.change-detail .info-list .info-item {
  padding: 0.2rem 0.2rem;
  display: flex;
  position: relative;
  width: 100%;
  overflow: hidden;
}
.change-detail .info-list .info-item:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background: #ddd;
  -webkit-transform: scaleY(0.5);
  -ms-transform: scaleY(0.5);
  -o-transform: scaleY(0.5);
  transform: scaleY(0.5);
  -webkit-transform-origin: 0 0;
  -moz-transform-origin: 0 0;
  -ms-transform-origin: 0 0;
  transform-origin: 0 0;
  left: .2rem;
}
.change-detail .info-list .info-item .key {
  width: 1.5rem;
}
.change-detail .info-list .info-item .val {
  color: #999999;
}
.change-detail .info-list .disc {
  height: .8rem;
  line-height: .8rem;
  color: #1890ff;
  padding: 0 .1rem;
}
.change-detail .commont-list {
  background: #ffffff;
  margin-top: .2rem;
}
.change-detail .commont-list .title {
  height: .9rem;
  line-height: .9rem;
  font-size: 14px;
  padding: 0 .2rem;
  position: sticky;
  background: #ffffff;
  z-index: 2;
  top: 0;
}
.change-detail .commont-list .title:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background: #ddd;
  -webkit-transform: scaleY(0.5);
  -ms-transform: scaleY(0.5);
  -o-transform: scaleY(0.5);
  transform: scaleY(0.5);
  -webkit-transform-origin: 0 0;
  -moz-transform-origin: 0 0;
  -ms-transform-origin: 0 0;
  transform-origin: 0 0;
}
.change-detail .commont-list .title i {
  color: #1890ff;
  margin-right: .1rem;
  margin-top: 5px;
}
.change-detail .commont-list .title .send-commont {
  position: absolute;
  font-size: 12px;
  color: #ffffff;
  background: #1890ff;
  top: .22rem;
  right: 0.2rem;
  line-height: 1;
  padding: .1rem .2rem;
  border-radius: 100px;
}
.change-detail .commont-list .item {
  padding: 0 .2rem;
  padding-bottom: .2rem;
  position: relative;
}
.change-detail .commont-list .item:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background: #ddd;
  -webkit-transform: scaleY(0.5);
  -ms-transform: scaleY(0.5);
  -o-transform: scaleY(0.5);
  transform: scaleY(0.5);
  -webkit-transform-origin: 0 0;
  -moz-transform-origin: 0 0;
  -ms-transform-origin: 0 0;
  transform-origin: 0 0;
}
.change-detail .commont-list .item .userinfo {
  display: flex;
  align-items: center;
  position: relative;
  height: 1.4rem;
}
.change-detail .commont-list .item .userinfo:after {
  margin-left: .2rem;
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background: #ddd;
  -webkit-transform: scaleY(0.5);
  -ms-transform: scaleY(0.5);
  -o-transform: scaleY(0.5);
  transform: scaleY(0.5);
  -webkit-transform-origin: 0 0;
  -moz-transform-origin: 0 0;
  -ms-transform-origin: 0 0;
  transform-origin: 0 0;
}
.change-detail .commont-list .item .userinfo img {
  width: 1rem;
  height: 1rem;
  border-radius: 100%;
  margin-right: .2rem;
}
.change-detail .commont-list .item .userinfo .name {
  font-size: 14px;
}
.change-detail .commont-list .item .userinfo .time {
  color: #999999;
  margin-top: .1rem;
}
.change-detail .commont-list .item .content {
  color: #555555;
  font-size: 13px;
}
.change-detail .commont-list .item .content .text {
  padding: .2rem 0;
}
.change-detail .commont-list .item .content .img-list {
  display: flex;
  justify-content: space-between;
}
.change-detail .commont-list .item .content .img-list img {
  width: 2.2rem;
  height: 2.2rem;
  border-radius: 10px;
}
.model-box {
  padding-bottom: .1rem;
  background: #ffffff;
}
.model-box input {
  width: 100%;
  position: relative;
  height: .8rem;
  font-size: 13px;
  padding-left: .2rem;
}
.model-box .status-box {
  border-bottom: 1px solid #eeeeee;
  border-top: 1px solid #eeeeee;
  padding: 0.2rem .2rem;
  display: flex;
}
.model-box .status-box .item {
  font-size: 12px;
  border: 1px solid #cccccc;
  line-height: 1;
  padding: .07rem .15rem;
  border-radius: 5px;
  margin-right: .3rem;
  color: #cccccc;
}
.model-box .status-box .success {
  background: #1890ff;
  color: #ffffff;
  border: none;
}
.model-box .submit {
  width: 7rem;
  height: .7rem;
  line-height: .7rem;
  background: #1890ff;
  color: #ffffff;
  border-radius: 100px;
  text-align: center;
  margin: 0 auto;
  margin-top: .2rem;
}
