/*
 增加不常规字体方案：一个参数的是正常比例换算方式
 floor--像下取整
 如：
 .font-responsive(40)
 结果：
 .font-40{
    font-size: 40px;
 }
 @media screen and (min-device-width: 375px) and (max-device-width: 413px) {
    .font-40{
      font-size: 46px;
    }
  }

  @media screen and (min-device-width: 414px){
    .font-40{
      font-size: 51px;
    }
  }
 */
/*
 增加不常规字体方案：三个参数的是媒体查询已输出的值为准
 如：
 .font-responsive(40,20,30)
 结果:
 .font-40{
  font-size: 40px;
 }
 @media screen and (min-device-width: 375px) and (max-device-width: 413px) {
    .font-40{
      font-size: 20px;
    }
  }
  @media screen and (min-device-width: 414px){
    .font-40{
      font-size: 30px;
    }
  }
*/
