/* Tab */
.shenghuo {
  font-size: 12px;
  color: #333333;
  background: #f2f2f2;
  padding-bottom: 1rem;
}
.shenghuo .swiper-slide img {
  width: 100%;
}
.shenghuo .btn {
  width: 200px;
}
.shenghuo .classify {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 20px;
  padding: 0 20px;
}
.shenghuo .chi {
  background: #ffc107;
}
.shenghuo .chi:hover {
  background: #ffc107;
}
.shenghuo .zhu {
  background: #795548;
}
.shenghuo .zhu:hover {
  background: #795548;
}
.shenghuo .xin {
  background: #689f38;
}
.shenghuo .xin:hover {
  background: #689f38;
}
.shenghuo .you {
  background: #039be5;
}
.shenghuo .you:hover {
  background: #039be5;
}
.shenghuo .gou {
  background: #ef6c00;
}
.shenghuo .gou:hover {
  background: #ef6c00;
}
.shenghuo .yu {
  background: #f4511e;
}
.shenghuo .yu:hover {
  background: #f4511e;
}
.shenghuo .user-item {
  padding: 15px;
  margin: 0 10px;
  background: #fff;
  margin-top: 5px;
  border-radius: 5px;
}
.shenghuo .user-item .base-info {
  display: flex;
}
.shenghuo .user-item .base-info img {
  min-width: 50px;
  max-width: 50px;
  height: 50px;
  margin-right: 10px;
  border-radius: 3px;
}
.shenghuo .user-item .base-info .user-info {
  flex-grow: 1;
}
.shenghuo .user-item .base-info .top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 10px;
}
.shenghuo .user-item .base-info .top .name {
  font-size: 14px;
  color: #333333;
}
.shenghuo .user-item .base-info .top .juli {
  font-size: 12px;
  color: #999999;
}
.shenghuo .user-item .base-info .score {
  font-size: 12px;
  color: #999999;
  margin-top: 7px;
}
.shenghuo .user-item .active-info {
  font-size: 12px;
  color: #999999;
  margin-top: 8px;
}
.shenghuo .user-item .active-info .active-name {
  color: #ff4d4f;
}
.shenghuo .user-item .active-info .number {
  color: #ff4d4f;
  margin: 0 2px;
}
.shenghuo .user-item .active-info .active-number {
  margin-top: 5px;
  color: #ff9100;
}
.shenghuo .am-list-body {
  background: #efeff4;
  padding-top: 3px;
}
.shenghuo .list {
  background: #fff;
  margin-top: .1rem;
}
.shenghuo .list .title {
  height: 30px;
  line-height: 30px;
  position: relative;
  padding-left: 6px;
  font-size: 13px;
}
.shenghuo .list .title:before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background: #ddd;
  -webkit-transform: scaleY(0.5);
  -ms-transform: scaleY(0.5);
  -o-transform: scaleY(0.5);
  transform: scaleY(0.5);
  -webkit-transform-origin: 0 0;
  -moz-transform-origin: 0 0;
  -ms-transform-origin: 0 0;
  transform-origin: 0 0;
}
.shenghuo .list .title:after {
  content: '';
  position: absolute;
  width: 3px;
  height: 12px;
  background: #1890ff;
  top: 50%;
  left: 0px;
  transform: translateY(-50%);
}
.shenghuo .list .item {
  display: flex;
  text-align: left;
  height: .7rem;
  line-height: .7rem;
  padding: 0 .25rem;
}
.shenghuo .list .item .key {
  width: 1.5rem;
}
.shenghuo .list .item .val {
  color: #999999;
}
.shenghuo .header {
  display: flex;
  padding: .2rem .2rem;
  background: #ffffff;
  position: relative;
}
.shenghuo .header:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background: #ddd;
  -webkit-transform: scaleY(0.5);
  -ms-transform: scaleY(0.5);
  -o-transform: scaleY(0.5);
  transform: scaleY(0.5);
  -webkit-transform-origin: 0 0;
  -moz-transform-origin: 0 0;
  -ms-transform-origin: 0 0;
  transform-origin: 0 0;
}
.shenghuo .header .logo {
  width: 1rem;
  height: 1rem;
  border-radius: 100%;
  margin-right: .2rem;
}
.shenghuo .header .info .title {
  font-size: 13px;
}
.shenghuo .header .info .number {
  margin-top: .1rem;
  color: #999999;
}
.shenghuo .header .status {
  display: flex;
  margin-top: .1rem;
}
.shenghuo .header .status .item {
  margin-right: .2rem;
  background: #999999;
  color: #ffffff;
  padding: .01rem .2rem;
  border-radius: 100px;
  font-size: 12px;
}
.shenghuo .header .status .item i {
  font-size: 12px;
  margin-right: .1rem;
}
.shenghuo .header .status .ing {
  background: #FFD301;
}
.shenghuo .start-btn {
  background: #108ee9;
  font-size: 14px;
  height: .9rem;
  margin: 0 auto;
  width: 100%;
  position: fixed;
  bottom: 0rem;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 0;
}
.shenghuo .start-btn:hover {
  background: #108ee9;
}
.shenghuo .gengjing-list .gengjing-item {
  background: #ffffff;
  margin: 0 .1rem;
  margin-top: .1rem;
  padding: 0.2rem .2rem;
  border-radius: 5px;
  position: relative;
}
.shenghuo .gengjing-list .gengjing-item .top {
  display: flex;
}
.shenghuo .gengjing-list .gengjing-item .top .title {
  font-size: 13px;
}
.shenghuo .gengjing-list .gengjing-item .top .time {
  color: #999999;
  margin-left: .2rem;
}
.shenghuo .gengjing-list .gengjing-item .user-name {
  color: #999999;
  margin-top: .1rem;
}
.shenghuo .gengjing-list .gengjing-item .status-box {
  display: flex;
}
.shenghuo .gengjing-list .gengjing-item .status-box .item {
  display: inline-block;
  padding: 0.02rem .2rem;
  border-radius: 100px;
  border: 1px solid #1890ff;
  color: #1890ff;
  font-size: 12px;
  line-height: 1;
  margin-top: .1rem;
  margin-right: .2rem;
}
.shenghuo .gengjing-list .gengjing-item .status-box .daiban {
  background: #49ca99;
  color: #ffffff;
  border: none;
}
.shenghuo .gengjing-list .gengjing-item .icon-right {
  position: absolute;
  right: 0.2rem;
  top: 50%;
  transform: translateY(-50%);
  color: #999999;
}
#krpanoSWFObject {
  width: 100%;
  height: 100px;
}
#pano {
  width: 100%;
  height: 100vh;
}
.am-tabs-default-bar-tab {
  font-size: 14px;
  height: 40px;
}
